<template>
  <div id="sub-header" class="row align-items-center">
    <div class="pl-3">
      <div id="universChoice" class="row px-3">
        <span>
          <input
            id="all"
            v-model="store.productUniverses"
            class="checkbox"
            type="checkbox"
            name="productUniverse"
            value="all"
            @change="changeUniverse"
          />
          <label for="all">All</label>
        </span>
        <span v-for="univers in universesList" :key="univers._id">
          <input
            :id="univers.universName"
            v-model="store.productUniverses"
            class="checkbox"
            type="checkbox"
            name="productUniverse"
            :value="univers.universName"
            @change="changeUniverse"
          />
          <label :for="univers.universName">{{ univers.universName }}</label>
        </span>
      </div>
    </div>
    <div class="col-md-3 ml-auto">
      <b-input-group id="searchBar" size="lg" placeholder="Search">
        <b-input-group-text slot="append">
          <img class="search-button" src="@/assets/search.png" alt />
        </b-input-group-text>
        <b-form-input v-model="searchTerm" placeholder="Search"></b-form-input>
      </b-input-group>
    </div>
  </div>
</template>

<script>
import Store from "@/store";
import axios from "axios";

export default {
  data() {
    return {
      store: Store.state,
      searchTerm: "",
      universesList: null
    };
  },
  watch: {
    searchTerm: function(term) {
      this.search(term);
    }
  },
  created() {
    axios.defaults.baseURL = process.env.VUE_APP_ROOT_API;
    const token = Store.getUserToken();
    if (token) {
      axios.defaults.headers.common["Cockpit-Token"] = token;
      axios
        .get("collections/get/universes")
        .then(response => {
          this.universesList = response.data.entries;
          this.loader = false;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  methods: {
    changeUniverse(event) {
      Store.changeUniverse(event.target.value);
      this.clearSelection();
    },
    search() {
      Store.addFilter(null, "globalSearch", this.searchTerm);
    }
  }
};
</script>

